<template>
    <el-form ref="form" :model="item.value" label-width="100px" style="text-align: left">
        <el-radio-group v-model="configArea" size="small">
            <el-radio-button label="query">防伪验证</el-radio-button>
            <el-radio-button label="success">首次查询</el-radio-button>
            <el-radio-button label="failure">多次查询</el-radio-button>
        </el-radio-group>
        <div style="height: 20px"></div>
        <div v-show="configArea === 'query'">
            <el-form-item label="按钮文字">
                <el-input size="small" v-model="item.value.buttonText" placeholder="马上验证"></el-input>
            </el-form-item>
            <el-form-item label="按钮颜色">
                <el-color-picker size="small" v-model="item.value.buttonColor"></el-color-picker>
            </el-form-item>
        </div>
        <div v-show="configArea === 'success'">
            <el-form-item label="查验结果">
                <el-input size="small" v-model="item.value.validTitle" placeholder="输入查验结果"></el-input>
            </el-form-item>
            <el-form-item label="防伪模板">
                <el-select v-model="item.value.successTemplate" placeholder="请选择" size="small" style="width: 100%">
                    <el-option v-for="t in successTemplateList" :key="t.id" :label="t.name" :value="t.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="防伪文字">
                <div style="white-space: pre-wrap;">{{ successTemplate }}</div>
            </el-form-item>
            <el-form-item label="语音播报">
                <el-switch v-model="item.value.validVoice"></el-switch>
            </el-form-item>
        </div>
        <div v-show="configArea === 'failure'">
            <el-form-item label="查验结果">
                <el-input size="small" v-model="item.value.invalidTitle" placeholder="输入查验结果"></el-input>
            </el-form-item>
            <el-form-item label="防伪模板">
                <el-select v-model="item.value.failureTemplate" placeholder="请选择" size="small" style="width: 100%">
                    <el-option v-for="t in failureTemplateList" :key="t.id" :label="t.name" :value="t.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="防伪文字">
                <div style="white-space: pre-wrap;">{{ failureTemplate }}</div>
            </el-form-item>
            <el-form-item label="查询记录">
                <el-switch v-model="item.value.queryHistory"></el-switch>
            </el-form-item>
        </div>
    </el-form>
</template>

<script>
import { getJsonResult } from '@/utils/server-wrapper'

export default {
    props: {
        item: Object
    },
    computed: {
        successTemplate() {
            if (!this.item.value.successTemplate) {
                return ''
            }
            const list = this.successTemplateList.filter(t => t.id === this.item.value.successTemplate)
            return (list[0] || {}).text
        },
        failureTemplate() {
            if (!this.item.value.failureTemplate) {
                return ''
            }
            const list = this.failureTemplateList.filter(t => t.id === this.item.value.failureTemplate)
            return (list[0] || {}).text
        }
    },
    data() {
        return {
            configArea: 'query',
            successTemplateList: [],
            failureTemplateList: []
        }
    },
    watch: {
        configArea() {
            this.item.value.configArea = this.configArea
        }
    },
    async mounted() {
        //this.successTemplateList = await getJsonResult(`/securityTemplate/listAll?scenario=success`)
        //this.failureTemplateList = await getJsonResult(`/securityTemplate/listAll?scenario=failure`)
    }
}
</script>
