import AbstractQuerySelect from './AbstractQuerySelect'
import { newPostJson } from '@/utils/server'
import eventBus from '@/utils/event-bus'

export default {
    data() {
        return {
            loading: false,
            currValue: null
        }
    },
    mixins: [AbstractQuerySelect],
    methods: {
        getLabel(item) {
            return item.name
        },
        remoteMethod(query) {
            if (query !== '') {
                this.loading = true
                //http://192.168.110.211:8081/businessObject/pageQuery
                const url = '/businessObject/pageQuery'
                newPostJson(url, { name: query }).then(res => {
                    if (res.code == 200) {
                        this.loading = false
                        this.options = res.rows
                    } else {
                        eventBus.$emit('showMessage', {
                            type: 'error',
                            message: res.msg
                        })
                        if (res.code == 2001) {
                            setTimeout(() => {
                                parent.window.location.href = '/#/login'
                            }, 1000)
                        }
                    }
                })
            } else {
                this.options = []
            }
        }
    },
    watch: {
        value: {
            handler(value) {
                if (value != null) {
                    const url = '/businessObject/pageQuery'
                    newPostJson(url, { id: value }).then(res => {
                        if (res.code == 200) {
                            this.options = res.rows
                        } else {
                            eventBus.$emit('showMessage', {
                                type: 'error',
                                message: res.msg
                            })
                            if (res.code == 2001) {
                                setTimeout(() => {
                                    parent.window.location.href = '/#/login'
                                }, 1000)
                            }
                        }
                    })
                }
                this.currValue = value
            },
            immediate: true
        }
    }
}
