<template>
    <!--标题栏组件属性设置-->
    <el-form ref="form" class="title-item-setting" :model="item.value" label-width="80px">
        <el-form-item label="标题">
            <el-input
                type="text"
                placeholder="请输入标题"
                size="small"
                v-model="item.value.title"
                maxlength="15"
                show-word-limit
                :rules="[{ required: true, message: '请输入标题', trigger: 'blur' }]"
            ></el-input>
        </el-form-item>
        <el-form-item label="图标">
            <el-upload
                class="avatar-uploader"
                :action="uploadUrl"
                :show-file-list="true"
                :file-list="fileList"
                :before-upload="beforeAvatarUpload"
                :on-success="handleAvatarSuccess"
                :on-change="onChangeToolFile"
                :on-remove="handleRemove"
                accept="image/jpeg, image/gif, image/png, image/jpg"
                list-type="picture"
            >
                <img v-if="item.value.icon" :src="item.value.icon" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
        </el-form-item>
        <el-form-item label="背景颜色">
            <el-color-picker v-model="item.value.bgcolor"></el-color-picker>
        </el-form-item>
    </el-form>
</template>
<script>
export default {
    data() {
        return {
            uploadUrl: 'https://xi.cn88555.com/api/base/upload-file',
            item: {
                value: {}
            },
            fileList: []
        }
    },
    methods: {
        asyncTitleInfo(item) {
            this.item = item
            this.fileList = []
            if (item.value.icon != '') {
                this.fileList.push({ url: item.value.icon, name: '' })
            }
        },
        handleAvatarSuccess(res, file, fileList) {
            this.item.value.icon = res.data
        },
        onChangeToolFile(file, fileList) {
            console.log(this.fileList, fileList)
            if (fileList.length > 0) {
                this.fileList = [fileList[fileList.length - 1].raw] // 这一步，是 展示最后一次选择的文件
            }
        },
        beforeAvatarUpload(file) {
            const isLt2M = file.size / 1024 / 1024 < 2

            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!')
                if (this.fileList.length == 1) {
                    this.fileList = []
                }
            }

            return isLt2M
        },
        handleRemove() {
            this.item.value.icon = ''
        }
    }
}
</script>
<style>
.title-item-setting {
    text-align: left;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409eff;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 80px;
    height: 80px;
    line-height: 80px;
    text-align: center;
}
.avatar {
    width: 80px;
    height: 80px;
    display: block;
}
</style>
