<template>
    <div class="home-config">
        <el-tabs v-model="activeName">
            <el-tab-pane label="属性配置" name="first">
                <defaultItem-Setting v-show="compFlg == ''"></defaultItem-Setting>
                <TitleItemSetting v-show="compFlg == 'templateHead'" ref="titleSetting"></TitleItemSetting>
                <MusicItemSetting v-show="compFlg == 'backMusic'" ref="musicSetting"></MusicItemSetting>
                <TextItemSetting v-show="compFlg == 'text'" :item="sortList" ref="textSetting"></TextItemSetting>
                <!-- <imageItem-Setting v-if="item.type == 'pictures'"></imageItem-Setting> -->
                <imageItem-Setting v-show="compFlg === 'pictures'" ref="imageSetting"></imageItem-Setting>
                <VideoItemSetting v-show="compFlg == 'video'" ref="videoSetting"></VideoItemSetting>
                <CarouselItemSetting v-show="compFlg == 'carousel'" ref="carouselSetting"></CarouselItemSetting>
                <StyleboxItemSetting v-show="compFlg == 'stylebox'" ref="styleboxSetting"></StyleboxItemSetting>
                <ProductInfoSetting
                    v-show="compFlg === 'productInfo'"
                    :item="item"
                    :sourceFlag="sourceFlag"
                ></ProductInfoSetting>
                <ValidateSetting v-show="compFlg === 'antiCounterfeit'" :item="item"></ValidateSetting>
                <!-- <TemplateIframeSetting v-show="compFlg === 'templateIframe'" :item="item"></TemplateIframeSetting> -->
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import DefaultItemSetting from '@/components/configuration/DefaultItemSetting'
import ImageItemSetting from '@/components/configuration/ImageItemSetting'
import VideoItemSetting from '@/components/configuration/VideoItemSetting'
import StyleboxItemSetting from '@/components/configuration/StyleboxItemSetting'
import TextItemSetting from '@/components/configuration/TextItemSetting'
import TitleItemSetting from '@/components/configuration/TitleItemSetting'
import CarouselItemSetting from '@/components/configuration/CarouselItemSetting'
import ProductInfoSetting from '@/components/configuration/ProductInfoSetting'
import ValidateSetting from '@/components/configuration/ValidateSetting'
import MusicItemSetting from '@/components/configuration/MusicItemSetting'

const noSettingComp = [
    'relatedProduct',
    'productIntroduction',
    'productBarcode',
    'productQualityGuaranteePeriod',
    'productPrice',
    'codeInformation',
    'productImages'
]

export default {
    data() {
        const defaultItem = {
            value: {},
            visible: true,
            id: -1,
            imgList: [require('@/assets/images/1.jpeg'), require('@/assets/images/1.jpeg')]
        }
        return {
            activeName: 'first',
            compFlg: '',
            defaultItem,
            item: defaultItem
        }
    },
    props: { sortList: Array, sourceFlag: String | Number },

    mounted() {
        console.log(this.sourceFlag)
    },
    watch: {
        sourceFlag(value) {
            this.item.value.sourceFlag = value
            console.log(this.item)
        }
    },
    components: {
        DefaultItemSetting,
        ImageItemSetting,
        VideoItemSetting,
        StyleboxItemSetting,
        TextItemSetting,
        TitleItemSetting,
        CarouselItemSetting,
        ProductInfoSetting,
        ValidateSetting,
        MusicItemSetting
    },
    methods: {
        switchingAttConf(item) {
            this.compFlg = item.type && noSettingComp.indexOf(item.type) < 0 ? item.type : ''
            if (item.type == 'text') {
                this.$refs.textSetting.asyncTextInfo(item)
            } else if (item.type === 'templateHead') {
                this.$refs.titleSetting.asyncTitleInfo(item)
            } else if (item.type === 'backMusic') {
                this.$refs.musicSetting.asyncMusicInfo(item)
            } else if (item.type === 'pictures') {
                this.$refs.imageSetting.asyncImageInfo(item)
            } else if (item.type === 'stylebox') {
                this.$refs.styleboxSetting.changeItem(item)
            } else if (item.type === 'video') {
                this.$refs.videoSetting.asyncVideoInfo(item)
            } else if (item.type === 'carousel') {
                this.$refs.carouselSetting.asyncCarouselInfo(item)
            }
            this.item = item
        },
        removeItem(item) {
            console.log('remove item;,', item)
            if (this.item === item) {
                this.item = this.defaultItem
                this.compFlg = ''
            }
        }
    }
}
</script>

