<template>
    <!--轮播图组件属性设置-->
    <el-form ref="form" style="text-align: left;" :model="item.value" label-width="80px">
        <el-form-item label="高度">
            <el-select v-model="item.value.height" size="mini" placeholder="200">
                <el-option
                    v-for="item in [200, 220, 250, 280, 300]"
                    :key="item"
                    :label="item"
                    :value="item + 'px'"
                ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="图片链接">
            <el-input v-model="imgUrl" placeholder="图片链接" size="small" @change="setImgUrl()"></el-input>
            <div style="font-size: 12px; color: #C0C0C0; line-height: 20px">
                可在连接中使用参数 {productName} (对象名) 或 {batchNo} (批次编号), {code} (标识), 比如
                https://www.baidu.com/s?wd={productName}
            </div>
        </el-form-item>

        <el-form-item label="轮播图">
            <el-upload
                class="upload-carouse"
                :action="uploadUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                :on-exceed="handleExceed"
                :file-list="fileList"
                :limit="4"
                accept="image/jpeg,image/gif,image/png,image/jpg"
                list-type="picture"
                :multiple="true"
            >
                <el-button size="mini" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">
                    <el-alert
                        title="只能上传jpg/jpeg/png/gif格式文件, 最多4张。"
                        type="info"
                        style="padding:0 0 0 5px;"
                        show-icon
                        :closable="false"
                    >
                    </el-alert>
                    <el-alert
                        title="为了达到最佳显示效果，请上传宽高比16：9的图片。"
                        type="info"
                        style="padding:0 0 0 5px; margin-top: 5px;"
                        show-icon
                        :closable="false"
                    >
                    </el-alert>
                </div>
            </el-upload>
        </el-form-item>
    </el-form>
</template>
<script>
import eventBus from '@/utils/event-bus'

export default {
    data() {
        return {
            uploadUrl: 'https://xi.cn88555.com/api/base/upload-file',
            // 控件用
            fileList: [],
            imgUrl: '',
            // 只记录图片地址
            images: [],
            item: {
                value: {
                    height: 200,
                    images: [],
                    url: ''
                }
            },
            disableUpload: false
        }
    },
    methods: {
        asyncCarouselInfo(item) {
            this.item = item
            this.fileList = []
            this.images = []
            this.imgUrl = this.item.value.url
            item.value.images.forEach(element => {
                this.fileList.push({ url: element, name: '' })
                this.images.push(element)
            })
        },
        handleRemove(file, fileList) {
            if (typeof file.response === 'undefined') {
                const fileUrl = file.url
                const index = this.item.value.images.findIndex(url => url === fileUrl)
                if (index > -1) {
                    this.item.value.images.splice(index, 1)
                    this.images.splice(
                        this.images.findIndex(item => item.url === fileUrl),
                        1
                    )
                    this.fileList = fileList
                }
            } else {
                const fileUrl = file.response.data
                const index = this.item.value.images.findIndex(url => url === fileUrl)
                if (index > -1) {
                    this.item.value.images.splice(index, 1)
                    this.images.splice(
                        this.images.findIndex(item => item.url === fileUrl),
                        1
                    )
                    this.fileList = fileList
                }
            }
        },
        handlePreview() {},
        handleSuccess(res, file, list) {
            console.log(res)
            if (res && (res.code === 200 || res.code === 0)) {
                this.images.push(res.data)
                this.item.value.images.push(res.data)
                this.item.value.url = this.imgUrl
            } else {
                this.$message.error('图片上传失败，请重试！')
            }
        },
        handleExceed() {
            eventBus.$emit('showMessage', {
                message: '最多上传4张图片'
            })
        },
        setImgUrl() {
            this.item.value.url = this.imgUrl
        }
    }
}
</script>
<style lang="scss" scoped>
.upload-carouse {
    text-align: left;
}
</style>
