<template>
    <div class="home-sort">
        <ul>
            <template v-for="(item, i) in sortList">
                <li
                    :ref="`item-${item.id}`"
                    :class="[item.id === shineId ? 'home-sort-item-select' : 'home-sort-item', 'flex-sb']"
                    v-if="CONFIG[item.type]"
                    :key="item.id"
                    @click="selectItem(item, i)"
                >
                    <div class="sort-opt-group">
                        <i
                            v-if="!CONFIG[item.type].must"
                            title="删除"
                            class="el-icon-close"
                            style="cursor: pointer;"
                            @click.stop="remove(item)"
                        ></i>
                        <i v-else style="width: 16px"></i>
                        <p class="home-sort-text">{{ CONFIG[item.type].title }}</p>
                    </div>

                    <div class="home-sort-icon">
                        <i v-show="i > 0" title="上移" class="el-icon-top" @click.stop="move('up', i, item)"></i>
                        <i
                            v-show="i < sortList.length - 1"
                            title="下移"
                            class="el-icon-bottom"
                            @click="move('down', i, item)"
                        ></i>
                        <i title="编辑" class="el-icon-edit-outline" @click.stop="selectItem(item, i)"></i>
                    </div>
                </li>
            </template>
        </ul>
    </div>
</template>

<script>
import CONFIG from '../config'

export default {
    data() {
        return {
            CONFIG,
            shineId: -1
        }
    },
    props: ['sortList'],
    components: {},
    methods: {
        move(type, i) {
            if (type === 'down') {
                this.swapArr(this.sortList, i, i + 1)
            } else {
                this.swapArr(this.sortList, i, i - 1)
            }
        },
        swapArr(arr, index1, index2) {
            arr[index1] = arr.splice(index2, 1, arr[index1])[0]
        },
        // 父组件调用这个方法来高亮要选中的项目
        hightItem(item) {
            this.shineId = item.id
            const el = this.$refs[`item-${item.id}`][0]
            this.$el.scrollTop = el.offsetTop
        },
        // 选择项目的回调
        selectItem(item, i) {
            this.$emit('selectItem', { item, i })
            this.shineId = item.id
        },
        remove(item) {
            this.$confirm('确定要删除该组件吗？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                closeOnClickModal: false,
                type: 'warning'
            }).then(async () => {
                this.$emit('removeItem', item)
            })
        }
    }
}
</script>

