<template>
    <div class="flex-v-center view-full">
        <div class="setting-sort">
            <!-- <a
                href="/productTemplate"
                style="width: 100px;
font-size:18px;text-align: left;color:#000;text-decoration-line: none;cursor: pointer;display: flex;
    align-items: center;margin-bottom: 10px;position: relative;top:-20px"
                ><img :src="backImg" alt="" style="width:22px;height:16px" />
                <span style="margin:0 16px;color:#D8D8D8">|</span> 返回</a
            > -->
            <div class="home-base-setting">
                <p class="home-base-setting-title">基本信息</p>
                <el-form :model="template" ref="validateForm" label-width="80px">
                    <el-form-item label="模板名称" prop="name" :rules="[{ required: true, message: '名称不能为空' }]">
                        <el-input
                            type="text"
                            size="small"
                            v-model="template.name"
                            placeholder="请输入模板名称"
                            autocomplete="off"
                            maxlength="15"
                            show-word-limit
                        ></el-input>
                    </el-form-item>
                    <el-form-item
                        label="绑定对象"
                        prop="productId"
                        :rules="[{ required: true, message: '请绑定对象信息' }]"
                    >
                        <product-select
                            v-model="template.productId"
                            style="width: 100%"
                            placeholder="请输入对象名称"
                            size="small"
                            @getFlag="handleProductChange"
                        >
                        </product-select>
                    </el-form-item>
                    <el-form-item class="mt20" style="margin-bottom:0">
                        <el-button type="warning" size="small" @click="submitForm('validateForm', true)"
                            >提交模板</el-button
                        >
                        <el-button type="primary" size="small" @click="submitForm('validateForm', false)"
                            >保存模板</el-button
                        >
                    </el-form-item>
                </el-form>
            </div>
            <home-setting ref="homeSetting" @updateItem="updateItem" :template="template"></home-setting>
            <p style="font-size: 16px; text-align: left; padding-left: 15px; margin-bottom: 5px;">显示栏目</p>
            <home-sort
                ref="homeSort"
                :sortList="template.items"
                @selectItem="selectItem"
                @removeItem="removeItem"
            ></home-sort>
        </div>
        <home-msg
            :sourceFlag="sourceFlag"
            @selectItem2="selectItem2"
            @selectItem="selectItem"
            @removeItem="removeItem"
            ref="homeMsg"
            :template="template"
            @changeSourceFlag="changeSourceFlag"
        ></home-msg>
        <home-config :sourceFlag="sourceFlag" :sortList="template.items" ref="homeConfig"></home-config>
    </div>
</template>

<script>
import Vue from 'vue'
import HomeSetting from './components/HomeSetting'
import HomeSort from './components/HomeSort'
import HomeMsg from './components/HomeMsg'
import HomeConfig from './components/HomeConfig'
import ProductSelect from '../commons/select/ProductSelect'
import CONFIG from './config'
import { getJsonResult, errorHint } from '@/utils/server-wrapper'
import { newPostJson } from '@/utils/server'
import eventBus from '@/utils/event-bus'
let id = -1
export default {
    data() {
        let template = {
            name: '',
            config: {
                getLocation: false,
                getWeixinUserInfo: false
            },
            items: [],
            productId: null
        }
        for (const key of Object.keys(CONFIG)) {
            const config = CONFIG[key]
            if (config.fixedSort || config.must) {
                template.items.push(this.newItem(key))
            }
        }
        template.items.sort((a, b) => (CONFIG[a.type].fixedSort || 100) - (CONFIG[b.type].fixedSort || 100))
        return {
            template,
            backImg: require('../../../assets/images/back.png'),
            sourceFlag: null
        }
    },
    computed: {
        isCreate() {
            return !!this.template.id
        }
    },
    methods: {
        selectItem(prop) {
            const item = prop.item
            this.$refs.homeMsg.selectItem(item.id)
            this.$refs.homeConfig.switchingAttConf(item)
        },
        selectItem2(prop) {
            const item = prop.item
            this.$refs.homeConfig.switchingAttConf(item)
        },
        handleProductChange(sourceFlag) {
            this.sourceFlag = sourceFlag
        },
        changeSourceFlag(sourceFlag) {
            this.sourceFlag = sourceFlag
        },
        updateItem(prop) {
            if (prop.flag) {
                const item = this.newItem(prop.item.type)
                this.template.items.push(item)
                const _this = this
                Vue.nextTick(function() {
                    _this.$refs.homeSort.hightItem(item)
                    _this.selectItem({ item, i: _this.template.items.length - 1 })
                })
            } else {
                this.template.items.splice(
                    this.template.items.findIndex(item => item.type === prop.item.type),
                    1
                )
            }
        },
        removeItem(prop) {
            const idx = this.template.items.findIndex(item => item.id === prop.id)
            const item = this.template.items[idx]
            this.$refs.homeConfig.removeItem(item)
            this.template.items.splice(idx, 1)

            // 同步数据到sort模块
            this.$refs.homeSetting.asyncItemInfo(prop)
        },
        newItem(type) {
            return {
                id: id--,
                type: type,
                visible: true,
                sort: 0,
                value: JSON.parse(JSON.stringify(CONFIG[type].defaultValue || {}))
            }
        },
        submitForm(formName, submit) {
            //校验模板名称是否重复
            // const d = { name: this.template.name, id: this.template.id }
            // postJson(`/productTemplate/queryTemplate`, d)
            //     .then(r => r.json())
            //     .then(r => (r.code === 200 ? Promise.resolve(r.data) : Promise.reject(r)))
            //     .then(d => {
            //         if (d.length > 0) {
            //             this.$message.error('模板名称已存在')
            //             return false
            //         } else {
            //             this.submitValid(formName, submit)
            //         }
            //     })
            //     .catch(e => errorHint(e))
            this.submitValid(formName, submit)
        },
        submitValid(formName, submit) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    const data = Object.assign({}, this.template)
                    data.items = []
                    let sort = 10

                    for (let item of this.template.items) {
                        console.log(JSON.parse(JSON.stringify(item)))
                        let validate = CONFIG[item.type].validate
                        if (validate) {
                            const msg = validate(item)
                            if (msg) {
                                this.$message.error(`${CONFIG[item.type].title} : ${msg}`)
                                this.selectItem({ item, i: this.template.items.indexOf(item) })
                                return
                            }
                        }
                        item = Object.assign({}, item)
                        item.sort = sort += 10
                        item.id = item.id < 0 ? null : item.id
                        data.items.push(item)
                    }
                    data.submitReview = submit
                    data.boId = data.productId
                    delete data.productId
                    console.log(data)
                    newPostJson(`/businessObjectTemplate/${this.template.id ? 'editTemplate' : 'add'}`, data)
                        .then(res => {
                            if (res.code == 200) {
                                eventBus.$emit('showMessage', {
                                    type: 'success',
                                    message: '添加成功'
                                })
                                setTimeout(() => {
                                    parent.window.history.back()
                                }, 500)
                            } else {
                                eventBus.$emit('showMessage', {
                                    type: 'error',
                                    message: res.msg
                                })
                                if (res.code == 2001) {
                                    setTimeout(() => {
                                        parent.window.location.href = '/#/login'
                                    }, 1000)
                                }
                            }
                        })
                        .catch(e => errorHint(e))
                } else {
                    console.log('error submit!!')
                    return false
                }
            })
        }
    },
    components: {
        // 配置
        HomeSetting,
        // 排序
        HomeSort,
        // 预览
        HomeMsg,
        // 设置
        HomeConfig,
        ProductSelect
    },
    // 路由前载入数据
    async beforeRouteEnter(to, from, next) {
        let template
        const templateId = to.query.id
        if (templateId) {
            template = await getJsonResult(`/businessObjectTemplate/detail?id=${templateId}`)
            if (template.config && typeof template.config === 'string') {
                template.config = JSON.parse(template.config)
            }
            template.productId = template.boId
            // template.productName = "测试对象编辑了"
            const items = []
            for (let raw of template.items) {
                if (raw) {
                    if (typeof raw.value === 'string') {
                        raw.value = JSON.parse(raw.value)
                    }
                    const defaultValue = CONFIG[raw.type].defaultValue
                    if (!(defaultValue instanceof Array)) {
                        for (let key of Object.keys(defaultValue)) {
                            if (raw.value[key] === undefined) {
                                raw.value[key] = defaultValue[key]
                            }
                        }
                    }
                    items.push(raw)
                }
            }
            template.items = items
            console.log(to.query.copy)
            if (to.query.copy) {
                template.id = null
                template.name = template.name + ' (副本)'
            }
        }

        next(vm => {
            if (template) {
                vm.$data.template = template
                vm.$refs.homeSetting.syncSettingInfo({ items: template.items })
            }
        })
    }
}
</script>

<style lang="scss" scoped>
.setting-sort {
    display: flex;
    flex-direction: column;
    // align-items: ;
    border-right: 1px solid #f2f2f2;
    height: 100%;
    width: 480px;
    padding: 20px;
    overflow-y: auto;
}
.home-base-setting {
    padding: 10px 15px;
    color: #666;
    font-size: 16px;
    text-align: left;
}
.home-base-setting .el-form .el-form-item {
    margin-bottom: 10px;
}
.home-base-setting .el-button {
    width: 48%;
}
</style>
