<template>
    <el-select
        :value="value"
        @change="onChange"
        :size="size"
        :placeholder="placeholder"
        :multiple="multiple"
        :disabled="disabled"
        :clearable="clearable"
        filterable
        remote
        :remote-method="remoteMethod"
        :loading="loading"
    >
        <el-option
            v-for="item in options"
            :key="getValue(item)"
            :label="getLabel(item)"
            :value="getValue(item)"
        ></el-option>
    </el-select>
</template>

<script>
export default {
    props: {
        value: [Number, String],
        size: {
            type: String,
            required: false
        },
        placeholder: {
            type: String,
            required: false
        },
        multiple: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean,
            default: false
        },
        clearable: {
            type: Boolean,
            default: false
        }
    },
    model: {
        prop: 'value',
        event: 'change'
    },
    data() {
        return {
            options: []
        }
    },
    watch: {
        value: {
            handler(value) {
                this.currValue = value
            },
            immediate: true
        }
    },
    methods: {
        onChange(v) {
            //console.log(v)
            for (let i = 0; i < this.options.length; i++) {
                if (this.options[i].id === v) {
                    this.$emit('getFlag', this.options[i].sourceFlag)
                }
            }
            this.currValue = v
            this.$emit('change', v)
        },
        getValue(item) {
            return item.id
        },
        getSelectedObject() {
            const list = this.options.filter(o => this.getValue(o) === this.currValue)
            return list.length === 0 ? null : list[0]
        }
    }
}
</script>
