<template>
    <!--属性设置 默认说明-->
    <div class="styleboxSetting" style="text-align: left;">
        <el-form ref="form" :model="form" :rules="rules" label-position="left" label-width="80px">
            <el-radio-group v-model="formStyle" size="small">
                <el-radio-button v-for="i in 4" :key="i" :label="i">样式{{ i }}</el-radio-button>
            </el-radio-group>

            <div v-for="(box, i) in row" :key="i">
                <h3 class="form-title">
                    栏目{{ i + 1 }}
                    <span style="font-size:12px;float:right">尺寸:{{ box.size }}</span>
                </h3>

                <el-form-item label="标题" :prop="`title${i}`">
                    <el-input size="small" v-model="box.title"></el-input>
                </el-form-item>
                <el-form-item label="链接">
                    <div>
                        <el-select
                            v-model="box.linkType"
                            placeholder="请选择"
                            @change="onLinkChange($event, box)"
                            size="small"
                            style="width: 100%"
                        >
                            <el-option
                                v-for="l in linkTypes"
                                :key="l.value"
                                :label="l.label"
                                :value="l.value"
                            ></el-option>
                        </el-select>
                        <div v-show="box.linkType === 'normal'">
                            <el-input v-model="box.href" size="small" placeholder="请输入自定义链接"></el-input>
                            <div style="font-size: 12px; color: #C0C0C0; line-height: 20px">
                                可在连接中使用参数 {productName} (对象名) 或 {batchNo} (批次编号), {code} (标识), 比如
                                https://www.baidu.com/s?wd={productName}
                            </div>
                        </div>
                        <el-input
                            v-show="box.linkType === 'kefuMobile'"
                            v-model="box.kefuMobile"
                            size="small"
                            placeholder="请输入客服电话"
                        ></el-input>
                        <el-upload
                            class="avatar-uploader"
                            :action="action"
                            v-show="box.linkType === 'qrCodeScan'"
                            :show-file-list="false"
                            :on-success="uploadQrCodeScan(box)"
                            accept=".jpg,.jpeg,.png,.gif,.JPG,.JPEG,.PBG,.GIF"
                        >
                            <img v-if="box.qrCodeImage" :src="box.qrCodeImage" class="avatar" />
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <div v-show="box.linkType === 'weixinWeapp'">
                            <el-input v-model="box.weixinWeappUserName" size="small" placeholder="原始ID"></el-input>
                            <div style="font-size: 12px; color: #C0C0C0; line-height: 20px">
                                进入微信小程序管理后台，在账号信息中查看小程序原始ID，如：gh_3eee678cd512
                            </div>
                            <el-input
                                v-model="box.weixinWeappPath"
                                size="small"
                                placeholder="小程序页面链接"
                            ></el-input>
                            <div style="font-size: 12px; color: #C0C0C0; line-height: 20px">
                                可在链接中使用参数 {productName} (对象名) 或 {batchNo} (批次编号), {code} (标识),
                                声明的页面路径必须添加.html后缀, 如 pages/{productName}/index.html
                            </div>
                        </div>
                    </div>
                </el-form-item>

                <!-- <el-form-item label="对齐方式">
                    <el-radio-group v-model="item.titleAlign" size="small">
                        <el-radio-button label="left">左对齐</el-radio-button>
                        <el-radio-button label="center">居中</el-radio-button>
                        <el-radio-button label="right">右对齐</el-radio-button>
                    </el-radio-group>
                </el-form-item>-->

                <el-form-item label="文字颜色">
                    <el-color-picker v-model="box.forecolor" size="small"></el-color-picker>
                </el-form-item>
                <el-form-item label="图标选择">
                    <el-switch
                        v-model="iconHistory[i].selectIcon"
                        inactive-color="#13ce66"
                        active-text="图标"
                        inactive-text="图片"
                        @change="changeIcon($event, i, box)"
                    ></el-switch>
                    <e-icon-picker
                        v-show="iconHistory[i].selectIcon"
                        class="styleboxItemSetting-iconPicker"
                        v-model="box.fonticon"
                        size="small"
                    />
                    <div
                        v-if="iconHistory[i].selectIcon && box.linkType === 'weixinWeapp'"
                        style="font-size: 12px; color: #C0C0C0; line-height: 20px;"
                    >
                        (微信小程序按钮暂不支持此类型图标展示)
                    </div>
                    <el-upload
                        v-show="!iconHistory[i].selectIcon"
                        class="avatar-uploader"
                        :action="action"
                        :show-file-list="false"
                        :on-success="uploadImgIcon(box)"
                        accept=".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF"
                    >
                        <img v-if="box.imgIcon" :src="box.imgIcon" class="avatar" />
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>

                <el-form-item label="背景选择">
                    <el-switch
                        v-model="bgHistory[i].selectColor"
                        inactive-color="#13ce66"
                        active-text="颜色"
                        inactive-text="图片"
                        @change="changeBg($event, i, box)"
                    ></el-switch>
                </el-form-item>
                <el-form-item v-show="bgHistory[i].selectColor" label="背景颜色">
                    <el-color-picker v-model="box.backcolor" size="small"></el-color-picker>
                </el-form-item>
                <el-form-item v-show="!bgHistory[i].selectColor" label="背景图">
                    <el-upload
                        class="avatar-uploader"
                        :action="action"
                        :show-file-list="false"
                        :on-success="uploadImg(box)"
                        accept=".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF"
                    >
                        <img v-if="box.backimage" :src="box.backimage" class="avatar" />
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>

<script>
export default {
    data() {
        const _this = this
        function titleValidator(idx) {
            return [
                {
                    validator: (rule, value, callback) => {
                        let config = _this.item.value.rows[0][idx]
                        if (config.title && config.title.length >= 6) {
                            config.title = config.title.substring(0, 6)
                            callback(new Error('标题长度不要超过6个字符'))
                        } else {
                            callback()
                        }
                    },
                    trigger: 'change'
                }
            ]
        }
        return {
            bgColor: true,
            action: 'https://xi.cn88555.com/api/base/upload-file',
            item: {
                value: {
                    rows: [[]]
                }
            },
            rules: {
                title0: titleValidator(0),
                title1: titleValidator(1),
                title2: titleValidator(2)
            },
            form: {},
            formStyle: 1,
            linkTypes: [
                { value: 'none', label: '无链接' },
                { value: 'normal', label: '自定义' },
                { value: 'weixinWeapp', label: '微信小程序' },
                { value: 'suretyService', label: '售后' },
                { value: 'warranty', label: '质保' },
                { value: 'companyIntroduction', label: '公司介绍' },
                { value: 'instruction', label: '使用说明' },
                { value: 'qrCodeScan', label: '关注微信 (上传关注二维码)' },
                { value: 'kefuMobile', label: '客服电话' },
                { value: 'contactUs', label: '联系我们' },
                { value: 'industrialMall', label: '工业商城' },
                { value: 'officialQuery', label: 'idis 官网查询' }
            ],
            historyData: [],
            defaultStyles: [
                {
                    title: '标题',
                    href: 'https://',
                    fonticon: 'menu',
                    forecolor: '#FFFFFF',
                    titleAlign: 'center',
                    backcolor: '#004da1',
                    linkType: 'normal'
                },
                {
                    title: '标题',
                    href: 'https://',
                    fonticon: 'menu',
                    forecolor: '#FFFFFF',
                    titleAlign: 'center',
                    backcolor: '#004da1',
                    linkType: 'normal'
                },
                {
                    title: '标题',
                    href: 'https://',
                    fonticon: 'menu',
                    forecolor: '#FFFFFF',
                    titleAlign: 'center',
                    backcolor: '#004da1',
                    linkType: 'normal'
                }
            ],
            bgHistory: [],
            iconHistory: []
        }
    },
    computed: {
        row() {
            return this.item.value.rows[0]
        }
    },
    watch: {
        formStyle(newValue) {
            const newCellCount = this.getCellCount(newValue)
            const oldCellCount = this.row.length
            if (oldCellCount < newCellCount) {
                for (let i = oldCellCount; i < newCellCount; i++) {
                    this.row.push(Object.assign({}, this.defaultStyles[i], this.historyData[i]))
                }
            } else if (oldCellCount > newCellCount) {
                for (let i = newCellCount; i < oldCellCount; i++) {
                    this.historyData[i] = this.row[i]
                }
                this.row.splice(newCellCount, oldCellCount - newCellCount)
            }

            if (newValue === 1) {
                this.row[0].size = this.row[1].size = this.row[2].size = 1
            } else if (newValue === 2) {
                this.row[0].size = 1
                this.row[1].size = 2
            } else if (newValue === 3) {
                this.row[0].size = 2
                this.row[1].size = 1
            } else {
                this.row[0].size = 3
            }
        }
    },
    methods: {
        changeItem(item) {
            if (this.item === item) {
                return
            }
            this.historyData = []
            this.item = item
            this.formStyle = this.getFormStyle(this.row)

            this.bgHistory = []
            this.iconHistory = []
            for (let i = 0; i < 3; i++) {
                const currValue = this.row[i] || this.defaultStyles[i]
                if (currValue.backcolor && currValue.backimage) {
                    currValue.backcolor = null
                }
                this.$set(
                    this.bgHistory,
                    i,
                    Object.assign({
                        selectColor: !!currValue.backcolor,
                        backimage: currValue.backimage,
                        backcolor: currValue.backcolor
                    })
                )
                this.$set(
                    this.iconHistory,
                    i,
                    Object.assign({
                        selectIcon: !!currValue.fonticon,
                        fonticon: currValue.fonticon,
                        imgIcon: currValue.imgIcon
                    })
                )
            }
        },
        getFormStyle(row) {
            if (row.length == 3) {
                return 1
            } else if (row.length === 1) {
                return 4
            } else if (row[0].size == 1) {
                return 2
            } else {
                return 3
            }
        },
        getCellCount(formStyle) {
            if (formStyle === 1) {
                return 3
            } else if (formStyle === 4) {
                return 1
            } else {
                return 2
            }
        },
        uploadImg(box) {
            return resp => {
                this.$set(box, 'backimage', resp.data)
            }
        },
        uploadImgIcon(box) {
            return resp => {
                this.$set(box, 'imgIcon', resp.data)
            }
        },
        uploadQrCodeScan(box) {
            return resp => {
                console.log('uploaded', resp)
                this.$set(box, 'qrCodeImage', resp.data)
            }
        },
        onLinkChange(value, box) {
            if (value !== 'normal') {
                box.href = ''
            }
            delete box.weixinWeappUserName
            delete box.weixinWeappPath
        },
        changeBg(value, i, box) {
            let his = this.bgHistory[i]
            if (value) {
                his.backimage = box.backimage
                this.$set(box, 'backcolor', his.backcolor)
                this.$set(box, 'backimage', null)
            } else {
                his.backcolor = box.backcolor
                this.$set(box, 'backcolor', null)
                this.$set(box, 'backimage', his.backimage)
            }
        },
        changeIcon(value, i, box) {
            let his = this.iconHistory[i]
            if (value) {
                his.imgIcon = box.imgIcon
                this.$set(box, 'imgIcon', null)
                this.$set(box, 'fonticon', his.fonticon)
            } else {
                his.fonticon = box.fonticon
                this.$set(box, 'imgIcon', his.imgIcon)
                this.$set(box, 'fonticon', null)
            }
        }
    }
}
</script>

