<template>
    <!--图片组件属性设置-->
    <el-form ref="form" :model="item.value" label-width="80px">
        <el-form-item label="链接" prop="link">
            <el-input v-model="item.value.link" placeholder="(以http/https 开头)" size="small"></el-input>
            <div style="font-size: 12px; color: #C0C0C0; line-height: 20px;text-align: left;">
                可在连接中使用参数 {productName} (对象名) 或 {batchNo} (批次编号), {code} (标识), 比如
                https://www.baidu.com/s?wd={productName}
            </div>
        </el-form-item>
        <el-form-item label="图片">
            <el-upload
                class="upload-carouse"
                :action="uploadUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                :on-exceed="handleExceed"
                :file-list="fileList"
                :limit="10"
                accept="image/jpeg,image/gif,image/png,image/jpg"
                list-type="picture"
                :multiple="true"
            >
                <el-button size="mini" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">
                    <el-alert
                        title="只能上传jpg/jpeg/png/gif格式文件"
                        type="info"
                        style="padding:0 0 0 5px"
                        show-icon
                        :closable="false"
                    >
                    </el-alert>
                </div>
            </el-upload>
        </el-form-item>
    </el-form>
</template>

<script>
import eventBus from '@/utils/event-bus'

export default {
    data() {
        return {
            uploadUrl: 'https://xi.cn88555.com/api/base/upload-file',
            // 控件用
            fileList: [],
            // 只记录图片地址
            images: [],
            item: {
                value: {}
            }
        }
    },
    methods: {
        asyncImageInfo(item) {
            this.item = item
            this.fileList = []
            this.images = []

            item.value.images.forEach(element => {
                this.fileList.push({ url: element, name: '' })
                this.images.push(element)
            })
        },
        handleRemove(file, fileList) {
            this.item.value.images.splice(
                this.item.value.images.findIndex(url => url === file.url),
                1
            )
            this.images.splice(
                this.images.findIndex(item => item.url === file.url),
                1
            )
            this.fileList = fileList
        },
        handlePreview(file) {
            console.log(file)
        },
        handleSuccess(res) {
            if (res && (res.code === 200 || res.code === 0)) {
                this.images.push(res.data)
                this.item.value.images.push(res.data)
            } else {
                this.$message.error('图片上传失败，请重试！')
            }
        },
        handleExceed() {
            eventBus.$emit('showMessage', {
                message: '最多上传10张图片'
            })
        }
    }
}
</script>
<style lang="scss" scoped>
.upload-carouse {
    text-align: left;
}
</style>
