<script>
import insImage from '@/assets/images/template-video-pic.jpg'

export default {
    data() {
        return {
            errorMsg: '',
            helpVisible: false,
            uploadUrl: 'https://xi.cn88555.com/api/base/upload-file',
            fileList: [],
            item: {
                value: {
                    iframeCode: ''
                }
            },
            insImage
        }
    },
    methods: {
        asyncVideoInfo(item) {
            this.item = item
        },
        handleRemove(file, fileList) {
            console.log(file, fileList)
        },
        handlePreview(file) {
            console.log(file)
        },
        setInputValue() {
            let url = this.item.value.iframeCode
            let match = (url || '').match(/src="([^"]+)"/)
            if (!match || !match[0]) {
                this.errorMsg = '没有检测到视频地址'
                return
            } else {
                this.errorMsg = ''
            }
            this.item.value.src = match[1]
        },
        triggerHelpPanel() {
            this.helpVisible = !this.helpVisible
        }
    },
    mounted() {
        // 如果组件变更了则
        this.$refs.helpPanel.style.height = window.innerHeight - 191 + 'px'
    }
}
</script>

