<template>
    <!--背景音乐栏组件属性设置-->
    <el-form ref="form" class="title-item-setting" :model="item.value" label-width="80px">
        <el-form-item label="音频">
            <el-upload
                :action="uploadUrl"
                :show-file-list="true"
                :file-list="fileList"
                :on-change="handleChange"
                :before-upload="beforeAvatarUpload"
                :on-success="handleAvatarSuccess"
                :on-remove="handleRemove"
                accept="audio/mp3, audio/ogg, audio/wav, audio/ape"
                list-type="audio"
            >
                <el-button size="small" type="primary">点击上传</el-button>
                <audio v-if="item.value.music" :src="item.value.music" />
                <div slot="tip" class="el-upload__tip">
                    <el-alert
                        title="只能上传1个mp3/ogg/wav/ape文件，且不超过10MB"
                        type="info"
                        style="padding:0 0 0 5px"
                        show-icon
                        :closable="false"
                    >
                    </el-alert>
                </div>
            </el-upload>
        </el-form-item>
        <el-form-item label="背景颜色">
            <el-color-picker v-model="item.value.bgcolor"></el-color-picker>
        </el-form-item>
    </el-form>
</template>
<script>
export default {
    data() {
        return {
            uploadUrl: 'https://xi.cn88555.com/api/base/upload-file',
            item: {
                value: {}
            },
            fileList: []
        }
    },
    methods: {
        asyncMusicInfo(item) {
            this.item = item
            this.fileList = []
            if (item.value.music != '') {
                var url = item.value.music
                var index = url.lastIndexOf('/')
                this.fileList.push({ url: url, name: url.substr(index + 1, url.length) })
            }
        },
        handleAvatarSuccess(res) {
            this.item.value.music = res.data
        },
        handleChange(file, fileList) {
            if (fileList.length > 0) {
                this.fileList = [fileList[fileList.length - 1]] //展示最后一次选择的文件
            }
        },
        handleRemove() {
            this.item.value.music = ''
        },
        beforeAvatarUpload(file) {
            const isLt2M = file.size / 1024 / 1024 < 10

            if (!isLt2M) {
                this.fileList = []
                this.$message.error('上传音频大小不能超过 10MB!')
            }
            return isLt2M
        }
    }
}
</script>
<style>
.title-item-setting {
    text-align: left;
}
</style>
