<template>
    <div class="home-setting">
        <p class="home-setting-title">配置信息</p>
        <ul class="ml70">
            <li class="home-setting-item">
                <div class="flex-v-center">
                    <p class="home-setting-text">获取用户</p>
                    <el-switch v-model="template.config.getWeixinUserInfo" :width="38"> </el-switch>
                </div>
            </li>
            <li class="home-setting-item">
                <div class="flex-v-center">
                    <p class="home-setting-text">导航栏</p>
                    <el-switch v-model="template.config.showBottom" :width="38"> </el-switch>
                </div>
            </li>
            <li class="home-setting-item " v-for="item in settingSelect" :key="item.id">
                <div class="flex-v-center">
                    <p class="home-setting-text">{{ item.title }}</p>
                    <el-switch
                        :v-model="chooseItem.has(item.type)"
                        :value="chooseItem.has(item.type)"
                        :width="38"
                        @change="updateItem(item, $event)"
                    >
                    </el-switch>
                </div>
            </li>
        </ul>
        <ul class="ml70">
            <li
                :class="['home-edit-item', 'flex-sb', chooseItem.has(item.type) ? 'home-edit-item-active' : '']"
                v-for="item in settingEdit"
                :key="item.id"
                @click="updateItem(item, true)"
            >
                <div class="flex-v-center">
                    <p class="home-setting-text ta-c" style="margin-right:0;width: 100% !important;">
                        <i :class="['icon', 'iconfont', iconStyleIcon[item.type]]"></i>{{ item.title }}
                    </p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import CONFIG from '../config'

export default {
    props: {
        template: Object
    },
    name: 'Home',
    data() {
        return {
            settingSelect: [],
            settingEdit: [],
            // 选择项目的计数. key 是配置名称, value 是次数
            chooseItem: new Map(),
            iconStyleIcon: {
                stylebox: 'el-icon-s-grid',
                video: 'el-icon-video-play',
                pictures: 'el-icon-picture-outline',
                text: 'el-icon-document'
                // ,templateIframe: 'el-icon-document'
            }
        }
    },
    components: {},
    mounted() {
        // 载入需要配置的选项, 必选的不显示, 限制为1 的显示为开关类型, 其余的显示为按钮类型
        for (const key of Object.keys(CONFIG)) {
            const config = CONFIG[key]
            if (config.must) {
                continue
            }
            if (config.limit === 1) {
                this.settingSelect.push(config)
            } else {
                this.settingEdit.push(config)
            }
        }
    },
    methods: {
        // 修改时初始化数据
        syncSettingInfo(data) {
            data.items.forEach(element => {
                if (this.chooseItem.has(element.type)) {
                    this.chooseItem.set(element.type, this.chooseItem.get(element.type) + 1)
                } else {
                    this.chooseItem.set(element.type, 1)
                }
            })
            this.$forceUpdate()
        },
        // 更新数量 (添加/ 删除)
        updateItem(item, event) {
            // 开关属性（移除还是添加）true 新增/ false 移除
            let flag = event === undefined ? true : event
            let prop = { item: item, flag: flag }
            if (!this.chooseItem.has(item.type)) {
                // 新增
                this.chooseItem.set(item.type, 1)
                this.$emit('updateItem', prop)
                this.$forceUpdate()
            } else {
                // 删除
                if (item && item.limit === 1) {
                    // switch 类型判断是否需要删除
                    if (event) {
                        this.chooseItem.set(item.type, 1)
                    } else {
                        this.chooseItem.delete(item.type)
                    }
                    this.$emit('updateItem', prop)
                    this.$forceUpdate()
                    return
                }

                if (this.chooseItem.has(item.type) && this.chooseItem.get(item.type) < item.limit) {
                    this.chooseItem.set(item.type, this.chooseItem.get(item.type) + 1)
                    this.$emit('updateItem', prop)
                    this.$forceUpdate()
                } else {
                    this.$message.error('最多添加' + item.limit + '条')
                }
            }
        },

        /**
         * 同步组件已选择信息delete
         */
        asyncItemInfo(item) {
            if (!this.chooseItem.has(item.type)) {
                this.$forceUpdate()
                return
            }
            if (this.chooseItem.has(item.type) && this.chooseItem.get(item.type) > 1) {
                this.chooseItem.set(item.type, this.chooseItem.get(item.type) - 1)
            } else {
                this.chooseItem.delete(item.type)
            }
            this.$forceUpdate()
        }
    }
}
</script>

